import React, { useState, useEffect } from 'react';
import {
  App,
  AccordionContent,
  BlockFooter,
  Block,
  Button,
  Card,
  CardContent,
  Chip,
  View,
  Page,
  Preloader,
  Navbar,
  LoginScreen,
  LoginScreenTitle,
  List,
  ListInput,
  ListItem,
  NavTitle,
  f7
} from 'framework7-react';
import axios from 'axios';

const GA_CLIENT_ID = '1092393785652-eluon044pa7rn4mdhus9jct1j0t9cua9.apps.googleusercontent.com';

function MyApp() {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [listEditorValue, setListEditorValue] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [transitionProgress, setTransitionProgress] = useState(0);
  const [hasFirstTransition, setHasFirstTransition] = useState(false);

  useEffect(() => {
    const token = localStorage.getItem('jwt_token');
    if (token) {
      setIsAuthenticated(true);
    }

    window.handleCredentialResponse = handleCredentialResponse;

    const script = document.createElement('script');
    script.src = "https://accounts.google.com/gsi/client";
    script.async = true;
    script.defer = true;
    document.body.appendChild(script);

    return () => {
      document.body.removeChild(script);
    };
  }, []);

  const handleCredentialResponse = async (response) => {
    const googleToken = response.credential;
    setIsLoading(true);
    try {
      const res = await axios.post('http://localhost:8000/token', {
        username: 'not_used',
        password: googleToken
      });
      localStorage.setItem('jwt_token', res.data.access_token);
      setIsAuthenticated(true);
      setIsLoading(false);
    } catch (error) {
      console.error('Authentication failed:', error);
      setIsAuthenticated(false);
      setIsLoading(false);
    }
  };

  const helpHandler = () => {
    if (!listEditorValue.trim()) return;
    document.querySelector('.page-content').scrollTo(0, 0)


    console.log('Help requested:', listEditorValue);
    setIsLoading(true);
    f7.preloader.show();

    setTimeout(() => {
      f7.preloader.hide();
      setIsLoading(false);
      startTransitionAnimation();
    }, 3000);
  };

  const startTransitionAnimation = () => {
    let startTime = null;
    const animateTransition = (timestamp) => {
      if (!startTime) startTime = timestamp;
      const progress = Math.min((timestamp - startTime) / 2000, 1);

      setTransitionProgress(progress);

      if (progress < 1) {
        requestAnimationFrame(animateTransition);
      } else {
        setTransitionProgress(0);
        setHasFirstTransition(true);
      }
    };

    requestAnimationFrame(animateTransition);
  };

  return (
    <App name="Cambridge Support" theme="ios">
      {!isAuthenticated ? (
        <LoginScreen id="my-login-screen" opened={!isAuthenticated}>
          <View>
            <Page loginScreen>
              <LoginScreenTitle>Login with Google</LoginScreenTitle>
              <div align="center" style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100px' }}>
                <div
                  id="g_id_onload"
                  data-client_id={GA_CLIENT_ID}
                  data-callback="handleCredentialResponse"
                  data-auto_prompt="false"
                  className="g_id_signin"
                  data-type="standard"
                  style={{
                    width: '100%',
                    maxWidth: '250px',
                    height: '50px'
                  }}
                ></div>
              </div>
              <BlockFooter>
                Click the button above to sign in using your Google account (@cam.ac.uk)
              </BlockFooter>
            </Page>
          </View>
        </LoginScreen>
      ) : (
        <View main className="safe-areas" url="/">
          <Page>
            <Navbar>
              <NavTitle>Cambridge Support</NavTitle>
            </Navbar>
            <Block>
              <div
                className={`grid ${hasFirstTransition ? 'medium-grid-cols-2 gap-4' : 'grid-cols-1'}`}
                style={{ transition: 'all 0.05s ease' }}
              >
                <div
                  style={{
                    width: `${!hasFirstTransition ? (100 - (transitionProgress * 50)) : '100'}%`,
                    transition: `${!hasFirstTransition ? 'width 0.05s ease' : ''}`,
                  }}
                >
                  <List strongIos outlineIos style={{ marginTop: '0px', marginBottom: '0px', paddingRight: '16px' }}>
                    <ListInput
                      style={{
                        minHeight: '400px',
                        maxHeight: '400px',
                        width: '100%',
                        transition: 'all 5s ease',
                        overflowY: 'auto'
                      }}
                      label="Support Request"
                      type="textarea"
                      placeholder="Enter your support request here..."
                      resizable
                      value={listEditorValue}
                      onInput={(e) => setListEditorValue(e.target.value)}
                      disabled={isLoading}
                    />
                  </List>
                  <List style={{ marginTop: '0px' }}>
                    <ListItem style={{ display: 'flex', justifyContent: 'flex-end' }}>
                      <Button
                        fill
                        preloader
                        onClick={helpHandler}
                        loading={isLoading}
                      >
                        Help
                      </Button>
                    </ListItem>
                  </List>
                </div>

                {hasFirstTransition && (
                  <div
                    style={{
                      opacity: 1,
                      transition: 'opacity 5s ease'
                    }}
                  >
                    <div className="grid medium-grid-cols-2 gap-4">
                      <div className="bg-gray-100 p-4 rounded">
                        <List dividersIos strong inset style={{ marginTop: '0px', marginBottom: '16px'}} accordionList>
                            <ListItem accordionItemOpened accordionItem title="Possible solution">
                                <AccordionContent>
                                    <Block style={{ marginBottom: '16px'}}>
                                        <Preloader />
                                    </Block>
                                </AccordionContent>
                            </ListItem>
                        </List>
                        <List dividersIos strong inset style={{ marginTop: '0px', marginBottom: '16px'}} accordionList>
                            <ListItem accordionItem  title="Clarification questions">
                                <AccordionContent>
                                    <Block style={{ marginBottom: '16px'}}>
                                        <Preloader />
                                    </Block>
                                </AccordionContent>
                            </ListItem>
                        </List>
                        <List dividersIos strong inset style={{ marginTop: '0px', marginBottom: '16px'}} accordionList>
                            <ListItem accordionItem  title="Abstract solution">
                                <AccordionContent>
                                    <Block style={{ marginBottom: '16px'}}>
                                        <Preloader />
                                    </Block>
                                </AccordionContent>
                            </ListItem>
                        </List>
                      </div>
                      <div className="bg-gray-100 p-4 rounded">
                        <List dividersIos strong strongIos inset style={{ marginTop: '0px', marginBottom: '16px'}} accordionList>
                            <ListItem accordionItemOpened accordionItem title="Category, Sub, Service, Priority">
                                <AccordionContent>
                                    <Card className="data-table data-table-collapsible data-table-init">
                                      <CardContent padding={false}>
                                          <table>
                                            <tbody>
                                              <tr>
                                                <td className="label-cell">Category</td>
                                                <td className="numeric-cell"><Preloader /></td>
                                              </tr>
                                              <tr>
                                                <td className="label-cell">Subcategory</td>
                                                <td className="numeric-cell"><Preloader /></td>
                                              </tr>
                                              <tr>
                                                <td className="label-cell">Service</td>
                                                <td className="numeric-cell"><Preloader /></td>
                                              </tr>
                                              <tr>
                                                <td className="label-cell">Priority</td>
                                                <td className="numeric-cell"><Preloader /></td>
                                              </tr>
                                            </tbody>
                                        </table>
                                      </CardContent>
                                    </Card>
                                </AccordionContent>
                            </ListItem>
                        </List>
                        <List dividersIos strong strongIos inset style={{ marginTop: '0px', marginBottom: '16px'}} accordionList>
                            <ListItem accordionItem title="Summary">
                                <AccordionContent>
                                  <Block>
                                    <p>
                                        <Preloader />
                                    </p>
                                  </Block>
                                </AccordionContent>
                            </ListItem>
                        </List>
                        <List dividersIos strong strongIos inset style={{ marginTop: '0px', marginBottom: '16px'}} accordionList>
                            <ListItem accordionItem title="Tags">
                                <AccordionContent>
                                    <Block style={{ marginBottom: '16px'}} >
                                        <Chip text="Red Chip" color="red" />
                                        <Chip text="Green Chip" color="green" />
                                        <Chip text="Blue Chip" color="blue" />
                                        <Chip text="Orange Chip" color="orange" />
                                        <Chip text="Pink Chip" color="pink" />
                                     </Block>
                                </AccordionContent>
                            </ListItem>
                        </List>
                        <List dividersIos strong strongIos inset style={{ marginTop: '0px', marginBottom: '16px'}} accordionList>
                            <ListItem accordionItem title="Similar HEATs">
                                <AccordionContent>
                                    <List>
                                        <ListItem link="#">
                                            <Preloader />
                                        </ListItem>
                                     </List>
                                </AccordionContent>
                            </ListItem>
                        </List>
                      </div>
                    </div>
                  </div>
                )}
              </div>
            </Block>
          </Page>
        </View>
      )}
    </App>
  );
}

export default MyApp;
